._navbarIcon_1v2n2_1 {
  position: relative;
  display: flex;
  align-items: center;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  font-weight: 500;
}._navbarIcon_1v2n2_1:focus {
  outline: 2px solid transparent;
  outline-offset: 2px;
}._navbarIcon_1v2n2_1 {
  /* @apply hover:bg-interactive; */
  --tw-ring-inset: inset;
}

/* theme_toggle */
._topNavThemeToggle_1v2n2_14 {
  --background-content: #353535;

  svg {
    fill: var(--background-content);
    background: 0;
    border: 0;
    position: relative;
  }
}
.light-theme ._topNavThemeToggle_1v2n2_14 {
  --background-content: #fff;
}

._toggleCircle_1v2n2_28 {
  fill: currentColor;
  transition: transform 500ms ease-out;
}

/* moon shape - move circle left */
.light-theme ._toggleCircle_1v2n2_28 {
  transform: translateX(-12%);
}

._toggleSun_1v2n2_38 {
  fill: currentColor;
  transform-origin: center center;
  transition: transform 750ms cubic-bezier(0.11, 0.14, 0.29, 1.32);
}

.light-theme ._toggleSun_1v2n2_38 {
  transform: rotate(0.5turn);
}
